import {useState} from "nuxt/app";
import {ConnectionStatus, RecordId, StringRecordId, Surreal} from "surrealdb";
import {CONNECTION_DOMAIN} from "~/utils/connection";
import {S3} from "@aws-sdk/client-s3";
import {XMLParser} from "fast-xml-parser";
import type {User} from "~/types";

export const useApp = () => {
    return useState('app', () => initDb());
}
export const useFs = () => {
    return useState('fs', () => initFs());
}

let db: Surreal | undefined;

async function initDb(): Promise<Surreal | undefined> {
    if (db && db.connection && db.connection.status === ConnectionStatus.Connected) return db;
    db = new Surreal();
    const user: User = useCurrentUser().value;
    try {
        if (user) {
            let result = false;
            if (user.token) {
                const exp = JSON.parse(atob(user.token.split('.')[1])).exp;
                if (Date.now() / 1000 > exp) {
                    //已过期
                    useCurrentUser().value = undefined;
                    useRouter().push({path: '/signin'});
                }
                result = await db.connect(CONNECTION_DOMAIN, {auth: user?.token});
            } else {
                result = await db.connect(CONNECTION_DOMAIN, user);
            }
            console.log('login result:', result);
            if (result) {
                const session = await db.query("select * from $token");
                const userId = new StringRecordId(session[0][0].ID);
                if (userId.rid.indexOf(':') < 0) {
                    userId.rid = 'user:' + userId.rid;
                }
                const user = await db.select<User>(userId) || {
                    userName: session[0][0].ID,
                    avatar: "admin"
                };
                useCurrentUser().value = user;
                return db;
            }
            useRouter().push({path: '/signin'});
        } else {
            useRouter().push({path: '/signin'});
        }

    } catch (err) {
        console.error("Failed to connect to SurrealDB:", err);
        throw err;
    }
}


let fs;
const fxp = new XMLParser();
const initFs = async () => {
    if (fs) {
        return fs;
    }
    const user: User = useCurrentUser().value;
    if (user?.token) {
        const exp = JSON.parse(atob(user?.token.split('.')[1])).exp;
        if (Date.now() / 1000 > exp) {
            //已过期
            useCurrentUser().value = undefined;
            goSignin();
        }
        // POST https://minio.example.net?Action=AssumeRoleWithWebIdentity
        //     &WebIdentityToken=TOKEN
        // &Version=2011-06-15
        // &DurationSeconds=86000
        // &Policy={}
        const res = await fetch(`https://oss.cuckoox.cn?Action=AssumeRoleWithWebIdentity&WebIdentityToken=${user.token}&DurationSeconds=86000&Version=2011-06-15`
            , {method: "post"}
        )
        if (res.ok) {
            const resxml = await res.text();
            const {
                AccessKeyId,
                SecretAccessKey,
                SessionToken
            } = fxp.parse(resxml).AssumeRoleWithWebIdentityResponse?.AssumeRoleWithWebIdentityResult?.Credentials;
            const provider = async (proper) => {
                console.log('proper', proper);
                return {
                    accessKeyId: AccessKeyId,
                    secretAccessKey: SecretAccessKey,
                    sessionToken: SessionToken,
                };
            };

            const minioClient = new S3({
                endpointProvider: () => {
                    return {
                        url: new URL('https://oss.cuckoox.cn')
                    }
                },
                region: 'cn-cd-1',
                tls: true,
                credentials: provider
            });
            //   <?xml version="1.0" encoding="UTF-8"?>
            // <AssumeRoleWithWebIdentityResponse xmlns="https://sts.amazonaws.com/doc/2011-06-15/">
            //       <AssumeRoleWithWebIdentityResult>
            //       <AssumedRoleUser>
            //       <Arn/>
            //       <AssumeRoleId/>
            //       </AssumedRoleUser>
            //   <Credentials>
            //     <AccessKeyId>Y4RJU1RNFGK48LGO9I2S</AccessKeyId>
            //     <SecretAccessKey>sYLRKS1Z7hSjluf6gEbb9066hnx315wHTiACPAjg</SecretAccessKey>
            //     <Expiration>2019-08-08T20:26:12Z</Expiration>
            //     <SessionToken>eyJhbGciOiJIUzUxMiIsInR5cCI6IkpXVCJ9..ewHqKVFTaP-j_kgZrcOEKroNUjk10GEp8bqQjxBbYVovV0nHO985VnRESFbcT6XMDDKHZiWqN2vi_ETX_u3Q-w</SessionToken>
            //   </Credentials>
            // </AssumeRoleWithWebIdentityResult>
            //   <ResponseMetadata/>
            // </AssumeRoleWithWebIdentityResponse>
            fs = minioClient;
            return minioClient;
        }

        goSignin();
    }
}

const goSignin = () => {
    if (useRoute().path !== '/signin') {
        useRouter().push({path: '/signin'})
    }
}